.div,
.from,
.paris {
  position: relative;
  color: var(--primary-contrast);
}
.paris {
  flex: 1;
  font-size: var(--font-size-xl);
  letter-spacing: 0.02em;
  font-family: var(--components-button-large);
  text-align: left;
}
.div,
.from {
  text-align: right;
  display: inline-block;
}
.from {
  font-size: var(--font-size-base);
  letter-spacing: 0.04em;
  text-transform: uppercase;
  font-family: var(--components-button-large);
  width: 52.53px;
}
.div {
  font-size: var(--font-size-19xl);
  line-height: 38px;
  font-family: var(--font-baloo-bhai);
  width: 90.92px;
}
.destinationDetails,
.details {
  display: flex;
  box-sizing: border-box;
  justify-content: flex-start;
}
.details {
  width: 90.92px;
  height: 59px;
  flex-direction: column;
  padding: var(--padding-0) 0.000003814697265625px var(--padding-0)
    var(--padding-0);
  align-items: flex-end;
  gap: var(--gap-11xs);
}
.destinationDetails {
  width: 287.41px;
  flex-direction: row;
  padding: var(--padding-5xs) var(--padding-xs);
  align-items: flex-start;
}
.col1,
.pariscard {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}
.pariscard {
  width: 288px;
  height: 182px;
  overflow: hidden;
  flex-shrink: 0;
  padding: var(--padding-0) 0.29296875px;
  align-items: flex-start;
  justify-content: flex-start;
  background-image: url(/public/pariscard@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.col1 {
  cursor: pointer;
  border: 0;
  padding: var(--padding-0) var(--padding-base);
  background-color: transparent;
  width: 25%;
  align-items: center;
  justify-content: center;
}
@media screen and (max-width: 960px) {
  .col1 {
    padding: var(--padding-base);
    box-sizing: border-box;
    width: 50%;
  }
}
@media screen and (max-width: 420px) {
  .col1 {
    width: 100%;
  }
}
